
import { put, takeLatest } from "redux-saga/effects";
import loginActions  from "../../actions/auth/index";
import * as menuActions  from "../../actions/menu/index";

import menuGenerator from "../../../configs/menuGenerator"

import { store } from "../../storeConfig/store"
import { history } from "../../../history"

import { getUserByEmail } from "../../../utility/crud/auth.crud"

export function* saga() {
  yield takeLatest(loginActions.actionTypes.Login, function* loginSaga() {
    const user  = yield getUserByEmail();
    yield put(loginActions.actions.updateUser(user));
  });

  yield takeLatest(loginActions.actionTypes.UpdateUser, function* UpdateUserSaga() {
    const {
      auth: { user }
    } = store.getState();


    if(user && history.location.pathname === "/auth/login"){
      const menu = yield menuGenerator(user)

      yield put(menuActions.UpdateMenuConfig(menu.menuConfig))
      yield put(menuActions.UpdatePaths(menu.menuPaths))

      yield history.push("/")
    }
  });
}
