import * as API from "../utility/crud/app.crud"

const menuGenerator = async (user) => {

  let menuConfig = []
  let menuPaths = ['/']

  if (user) {

    menuConfig = [
      {
        id: "dashboard",
        title: "Dashboard",
        type: "item",
        icon: "Home",
        navLink: "/",
      }
    ];

    const getMainMenuQuery = `
      query {
        MainMenus {
          MENU_ID
          MENU_NAME
          ICON
          SUB_MENU {
            SUB_MENU_ID
            MAIN_MENU_ID
            SUB_MENU_NAME
            MENU_PATH
            }
            SUB_SUB_MENU {
              SUB_SUB_MENU_ID
              MAIN_MENU_ID
              SUB_SUB_MENU_NAME
              SUB_SUB_MENU_ICON
              ASSIGNMENTS {
                SUB_MENU_ASSIGNMENT_ID
                SUB_MENU_ID
                SUB_SUB_MENU_ID
                SUB_MENU {
                  SUB_MENU_ID
                  MAIN_MENU_ID
                  SUB_MENU_NAME
                  MENU_PATH
                }
              }
            }
        }
      }
    `;

    const mainMenus = await API.getData(getMainMenuQuery);

    const mainMenuData = mainMenus.data.data.MainMenus;

    let userReadPaths = [];

    user.USER_GROUPS.forEach(user_group => {
      user_group.GROUP_ROLES.forEach(group_role => {
        if (group_role.READ_PERMISSION) {
          userReadPaths.push(group_role.ROLE.SUB_MENU.MENU_PATH)
        }
      })
    });

    mainMenuData.forEach(data => {

      if (data.MENU_NAME !== "Dashboard") {

        const menuItem = {
          id: data.MENU_NAME.toLocaleLowerCase(),
          title: data.MENU_NAME,
          type: "collapse",
          icon: data.ICON,
          children: []
        }

        if (data.SUB_SUB_MENU.length) {
          data.SUB_SUB_MENU.forEach(sub_sub_menu => {

            let subSubMenuItem = {
              id: "sub-menu" + sub_sub_menu.SUB_SUB_MENU_ID,
              title: sub_sub_menu.SUB_SUB_MENU_NAME,
              type: "collapse",
              icon: sub_sub_menu.SUB_SUB_MENU_ICON,
              children: []
            }

            sub_sub_menu.ASSIGNMENTS.forEach(assignment => {
              if (userReadPaths.includes(assignment.SUB_MENU.MENU_PATH)) {

                menuPaths.push(`/${assignment.SUB_MENU.MENU_PATH}`)

                subSubMenuItem.children.push({
                  id: assignment.SUB_MENU.SUB_MENU_ASSIGNMENT_ID,
                  title: assignment.SUB_MENU.SUB_MENU_NAME,
                  type: "item",
                  icon: "Circle",
                  navLink: `/${assignment.SUB_MENU.MENU_PATH}`,
                })

              }
            });

            menuItem.children.push(subSubMenuItem)
          });
        } else {
          if (data.SUB_MENU.length) {
            data.SUB_MENU.forEach(item => {
              if (userReadPaths.includes(item.MENU_PATH)) {
              menuPaths.push(`/${item.MENU_PATH}`)

              menuItem.children.push({
                id: item.MENU_ID,
                title: item.SUB_MENU_NAME,
                type: "item",
                icon: "Circle",
                navLink: `/${item.MENU_PATH}`,
              })

            }

            })
          }

        }

        if (menuItem.children.length) {
          menuConfig.push(menuItem)
        }

        //menuConfig.push(menuItem);
      }

    });

  }
  // else {
  //   window.location = "/auth/logout";
  // }

  return { menuConfig, menuPaths }
}

export default menuGenerator;