
const initialMenuState = {
  menuConfig: [],
  menuPaths: []
}

const menuReducer = (state = initialMenuState, action) => {
  switch (action.type) {
    case "UPDATE_MENU_PATHS": {
      return { ...state, menuPaths: action.menuPaths }
    }
    case "UPDATE_MENU_CONFIG": {
      return { ...state, menuConfig: action.menuConfig }
    }
    default: {
      return state
    }
  }
}

export default menuReducer
