
export const actionTypes = {
  Login: "LOGIN_WITH_JWT",
  Logout: "LOGOUT_WITH_JWT",
  UpdateUser: "UPDATE_USER",
};

export const actions = {
  login: token => ({ type: actionTypes.Login, token }),

  logout: () => ({ type: actionTypes.Logout }),

  updateUser: user => ({ type: actionTypes.UpdateUser, user }),
};
