
import axios from "axios";

export const BASE_URL = "http://154.72.192.26:6600/api";
//export const BASE_URL = "http://localhost:6600/api";

export function getData(toGet) {
  return axios.post(BASE_URL, { query: toGet });
}

export function create(toCreate) {
  return axios.post(BASE_URL, { query: toCreate });
}

export function update(toUpdate) {
  return axios.post(BASE_URL, { query: toUpdate });
}

export function remove(toRemove) {
  return axios.post(BASE_URL, { query: toRemove });
}


