import axios from "axios";
import { BASE_URL } from "./app.crud";

export const LOGIN_URL = "http://154.72.192.26:6600/api/auth/login";
//export const LOGIN_URL = "http://localhost:6600/api/auth/login";

export let emailaddress = "";

export function login(email, passwd) {

  const loginQuery = `
    query {
      appUserLogin(email: "${email}", passwd: "${passwd}"){
        accessToken
        refreshToken
      }
    }
  `;

  emailaddress = email;
  return axios.post(LOGIN_URL, { query: loginQuery });
}

export function getAllUsers() {

  const getAppUsers = `
query {
  appUsers {
    USER_ID
    USER_NAME
    FIRST_NAME
    MIDDLE_NAME
    LAST_NAME
    EMAIL
    USER_GROUPS{
    GROUP{
        GROUP_ID
        GROUP_NAME
    }
    GROUP_ROLES{
      ACTIVE  
      READ_PERMISSION
        CREATE_PERMISSION
        UPDATE_PERMISSION
        DELETE_PERMISSION
        PRINT_PERMISSION
        APPROVE_PERMISSION
        ROLE {
        ROLE_ID
        ROLE_NAME
        SUB_MENU_ID
        SUB_MENU{
          SUB_MENU_ID
          MAIN_MENU_ID
          SUB_MENU_NAME
          MENU_PATH
        }
        }
    }
}
}
}
`;

  return axios.post(BASE_URL, { query: getAppUsers });
}

export function getUserByEmail() {
  return getAllUsers().then(response => response.data.data.appUsers.find(x => x.EMAIL === emailaddress))
}

