
export const actionTypes = {
  updateMenuPaths: "UPDATE_MENU_PATHS",
  updateMenuConfig: "UPDATE_MENU_CONFIG",
};

export const UpdatePaths = menuPaths => {
  return dispatch => dispatch({ type: actionTypes.updateMenuPaths, menuPaths })
}

export const UpdateMenuConfig = menuConfig => {
  return dispatch => dispatch({ type: actionTypes.updateMenuConfig, menuConfig })
}
